@import "../../theme";

.zen-badge {
    text-transform: uppercase;
    color: $white;
    font-size: 12px;
    line-height: 13px;
    padding: 4px 8px;
}
.zen-badge-primary {
    color: $white;
    background: $primary;
}
.zen-badge-warning {
    color: $white;
    background: $warning;
}
.zen-badge-danger {
    color: $white;
    background: $danger;
}