@import "../../theme";

.zen-tooltip {
    position: absolute;
    display: block;
    font-size: 12px;
    line-height: 13px;
    z-index: 1070;
    visibility: visible;
}
.zen-tooltip-hidden {
    display: none;
}
.zen-tooltip-placement-top,
.zen-tooltip-placement-topLeft,
.zen-tooltip-placement-topRight {
    padding: 4px 0 8px 0;
}
.zen-tooltip-placement-right,
.zen-tooltip-placement-rightTop,
.zen-tooltip-placement-rightBottom {
    padding: 0 4px 0 8px;
}
.zen-tooltip-placement-bottom,
.zen-tooltip-placement-bottomLeft,
.zen-tooltip-placement-bottomRight {
    padding: 8px 0 4px 0;
}
.zen-tooltip-placement-left,
.zen-tooltip-placement-leftTop,
.zen-tooltip-placement-leftBottom {
    padding: 0 8px 0 4px;
}
.zen-tooltip-inner {
    background: $white;
    box-shadow: $box-shadow;
    border-radius: $rounded-10;
    padding: 10px 8px;
}
.zen-tooltip-arrow {
    border-color: transparent;
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
}
.zen-tooltip-placement-top .zen-tooltip-arrow,
.zen-tooltip-placement-topLeft .zen-tooltip-arrow,
.zen-tooltip-placement-topRight .zen-tooltip-arrow {
    bottom: 4px;
    margin-left: -4px;
    border-top: 4px solid $white;
}
.zen-tooltip-placement-top .zen-tooltip-arrow {
    left: 50%;
}
.zen-tooltip-placement-topLeft .zen-tooltip-arrow {
    left: 15%;
}
.zen-tooltip-placement-topRight .zen-tooltip-arrow {
    right: 15%;
}
.zen-tooltip-placement-right .zen-tooltip-arrow,
.zen-tooltip-placement-rightTop .zen-tooltip-arrow,
.zen-tooltip-placement-rightBottom .zen-tooltip-arrow {
    border-width: 4px 4px 4px 0;
    border-right-color: white;
    left: 4px;
    margin-top: -4px;
}
.zen-tooltip-placement-right .zen-tooltip-arrow {
    top: 70%;
}
.zen-tooltip-placement-rightTop .zen-tooltip-arrow {
    top: 15%;
    margin-top: 0;
}
.zen-tooltip-placement-rightBottom .zen-tooltip-arrow {
    bottom: 15%;
}
.zen-tooltip-placement-left .zen-tooltip-arrow,
.zen-tooltip-placement-leftTop .zen-tooltip-arrow,
.zen-tooltip-placement-leftBottom .zen-tooltip-arrow {
    right: 4px;
    margin-top: -5px;
    border-width: 5px 0 5px 5px;
    border-left-color: #373737;
}
.zen-tooltip-placement-left .zen-tooltip-arrow {
    top: 50%;
}
.zen-tooltip-placement-leftTop .zen-tooltip-arrow {
    top: 15%;
    margin-top: 0;
}
.zen-tooltip-placement-leftBottom .zen-tooltip-arrow {
    bottom: 15%;
}
.zen-tooltip-placement-bottom .zen-tooltip-arrow,
.zen-tooltip-placement-bottomLeft .zen-tooltip-arrow,
.zen-tooltip-placement-bottomRight .zen-tooltip-arrow {
    top: 4px;
    margin-left: -5px;
    border-width: 0 5px 5px;
    border-bottom-color: #373737;
}
.zen-tooltip-placement-bottom .zen-tooltip-arrow {
    left: 50%;
}
.zen-tooltip-placement-bottomLeft .zen-tooltip-arrow {
    left: 15%;
}
.zen-tooltip-placement-bottomRight .zen-tooltip-arrow {
    right: 15%;
}