@import "../../theme";

.zen-dialog-mask {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #373737;
    background-color: rgba(55,55,55,.6);
    height: 100%;
    filter: alpha(opacity=50);
    z-index: 1050;
}
.zen-dialog-wrap {
    position: fixed;
    overflow: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    -webkit-overflow-scrolling: touch;
    outline: 0;
}
.zen-dialog {
    position: relative;
    width: 624px;
    max-width: 100%;
    margin: 30px auto;
}
.zen-dialog-lg {
    min-width: 642px;
    width: 1037px;
}
.zen-dialog-content {
    position: relative;
    background-color: $white;
    border: none;
    border-radius: 6px 6px;
    background-clip: padding-box;
}
.zen-dialog-header {
    padding-top: 52px;
}
.zen-dialog-title {
    font-weight: $weight-semibold;
    font-size: 24px;
    line-height: 26px;
    padding-left: 40px;
    padding-right: 40px;
}
.zen-dialog-close {
    position: absolute;
    right: 40px;
    top: 56px;
}
.zen-dialog-close-x::after {
    content: "X";
    font-weight: $weight-extralight;
}