@import "../../theme";
.zen-dropdown {
    position: absolute;
    left: -9999px;
    top: -9999px;
    z-index: 1050;
    display: block;
    font-size: 12px;
    line-height: 1.5;
}
.zen-dropdown::before {
    position: absolute;
    top: -4px;
    right: 0;
    bottom: -4px;
    left: -7px;
    z-index: -9999;
    opacity: 0.0001;
    content: ' ';
}
.zen-dropdown-hidden {
    display: none;
}
.zen-dropdown-menu {
    position: relative;
    margin: 0;
    padding: 4px 0;
    text-align: left;
    list-style-type: none;
    background-color: $white;
    background-clip: padding-box;
    border-radius: 2px;
    outline: none;
    box-shadow: $box-shadow;
}
.zen-dropdown-menu > li {
    margin: 0;
    padding: 0;
}
.zen-dropdown-menu:before {
    content: "";
    position: absolute;
    top: -4px;
    left: 0;
    width: 100%;
    height: 4px;
    background: $white;
}
.zen-dropdown-menu > .zen-dropdown-menu-item {
    position: relative;
    display: block;
    color: $primary-navy;
    clear: both;
    margin: 0;
    padding: 5px 12px;
    font-size: 14px;
    line-height: 22px;
    white-space: nowrap;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.zen-dropdown-menu > .zen-dropdown-menu-item:hover,
.zen-dropdown-menu > .zen-dropdown-menu-item-active,
.zen-dropdown-menu > .zen-dropdown-menu-item-selected {
    background: #f5f5f5;
}
.zen-dropdown-menu > .zen-dropdown-menu-item-disabled {
    color: #ccc;
    cursor: not-allowed;
    pointer-events: none;
}
.zen-dropdown-menu > .zen-dropdown-menu-item-disabled:hover {
    color: #ccc;
    background-color: #fff;
    cursor: not-allowed;
}
.zen-dropdown-menu > .zen-dropdown-menu-item:last-child {
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
}
.zen-dropdown-menu > .zen-dropdown-menu-item:first-child {
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.zen-dropdown-menu > .zen-dropdown-menu-item-divider {
    height: 1px;
    margin: 4px 0;
    overflow: hidden;
    line-height: 0;
    background-color: #f0f0f0;
}
.zen-dropdown-slide-up-enter,
.zen-dropdown-slide-up-appear {
    animation-duration: 0.3s;
    animation-fill-mode: both;
    transform-origin: 0 0;
    display: block !important;
    opacity: 0;
    animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
    animation-play-state: paused;
}
.zen-dropdown-slide-up-leave {
    animation-duration: 0.3s;
    animation-fill-mode: both;
    transform-origin: 0 0;
    display: block !important;
    opacity: 1;
    animation-timing-function: cubic-bezier(0.6, 0.04, 0.98, 0.34);
    animation-play-state: paused;
}
.zen-dropdown-slide-up-enter.zen-dropdown-slide-up-enter-active.zen-dropdown-placement-bottomLeft,
.zen-dropdown-slide-up-appear.zen-dropdown-slide-up-appear-active.zen-dropdown-placement-bottomLeft,
.zen-dropdown-slide-up-enter.zen-dropdown-slide-up-enter-active.zen-dropdown-placement-bottomCenter,
.zen-dropdown-slide-up-appear.zen-dropdown-slide-up-appear-active.zen-dropdown-placement-bottomCenter,
.zen-dropdown-slide-up-enter.zen-dropdown-slide-up-enter-active.zen-dropdown-placement-bottomRight,
.zen-dropdown-slide-up-appear.zen-dropdown-slide-up-appear-active.zen-dropdown-placement-bottomRight {
    animation-name: zenDropdownSlideUpIn;
    animation-play-state: running;
}
.zen-dropdown-slide-up-enter.zen-dropdown-slide-up-enter-active.zen-dropdown-placement-topLeft,
.zen-dropdown-slide-up-appear.zen-dropdown-slide-up-appear-active.zen-dropdown-placement-topLeft,
.zen-dropdown-slide-up-enter.zen-dropdown-slide-up-enter-active.zen-dropdown-placement-topCenter,
.zen-dropdown-slide-up-appear.zen-dropdown-slide-up-appear-active.zen-dropdown-placement-topCenter,
.zen-dropdown-slide-up-enter.zen-dropdown-slide-up-enter-active.zen-dropdown-placement-topRight,
.zen-dropdown-slide-up-appear.zen-dropdown-slide-up-appear-active.zen-dropdown-placement-topRight {
    animation-name: zenDropdownSlideDownIn;
    animation-play-state: running;
}
.zen-dropdown-slide-up-leave.zen-dropdown-slide-up-leave-active.zen-dropdown-placement-bottomLeft,
.zen-dropdown-slide-up-leave.zen-dropdown-slide-up-leave-active.zen-dropdown-placement-bottomCenter,
.zen-dropdown-slide-up-leave.zen-dropdown-slide-up-leave-active.zen-dropdown-placement-bottomRight {
    animation-name: zenDropdownSlideUpOut;
    animation-play-state: running;
}
.zen-dropdown-slide-up-leave.zen-dropdown-slide-up-leave-active.zen-dropdown-placement-topLeft,
.zen-dropdown-slide-up-leave.zen-dropdown-slide-up-leave-active.zen-dropdown-placement-topCenter,
.zen-dropdown-slide-up-leave.zen-dropdown-slide-up-leave-active.zen-dropdown-placement-topRight {
    animation-name: zenDropdownSlideDownOut;
    animation-play-state: running;
}
@keyframes zenDropdownSlideUpIn {
    0% {
        opacity: 0;
        transform-origin: 0% 0%;
        transform: scaleY(0);
    }
    100% {
        opacity: 1;
        transform-origin: 0% 0%;
        transform: scaleY(1);
    }
}
@keyframes zenDropdownSlideUpOut {
    0% {
        opacity: 1;
        transform-origin: 0% 0%;
        transform: scaleY(1);
    }
    100% {
        opacity: 0;
        transform-origin: 0% 0%;
        transform: scaleY(0);
    }
}
@keyframes zenDropdownSlideDownIn {
    0% {
        opacity: 0;
        transform-origin: 0 100%;
        transform: scaleY(0);
    }
    100% {
        opacity: 1;
        transform-origin: 0 100%;
        transform: scaleY(1);
    }
}
@keyframes zenDropdownSlideDownOut {
    0% {
        opacity: 1;
        transform-origin: 0 100%;
        transform: scaleY(1);
    }
    100% {
        opacity: 0;
        transform-origin: 0 100%;
        transform: scaleY(0);
    }
}
.zen-dropdown-arrow {
    position: absolute;
    border-width: 4px;
    border-color: transparent;
    box-shadow: 0 1px 5px #ccc;
    border-style: solid;
    transform: rotate(45deg);
}
.zen-dropdown-show-arrow.zen-dropdown-placement-top,
.zen-dropdown-show-arrow.zen-dropdown-placement-topLeft,
.zen-dropdown-show-arrow.zen-dropdown-placement-topRight {
    padding-bottom: 6px;
}
.zen-dropdown-show-arrow.zen-dropdown-placement-bottom,
.zen-dropdown-show-arrow.zen-dropdown-placement-bottomLeft,
.zen-dropdown-show-arrow.zen-dropdown-placement-bottomRight {
    padding-top: 6px;
}
.zen-dropdown-placement-top .zen-dropdown-arrow,
.zen-dropdown-placement-topLeft .zen-dropdown-arrow,
.zen-dropdown-placement-topRight .zen-dropdown-arrow {
    bottom: 4px;
    border-top-color: white;
}
.zen-dropdown-placement-top .zen-dropdown-arrow {
    left: 50%;
}
.zen-dropdown-placement-topLeft .zen-dropdown-arrow {
    left: 15%;
}
.zen-dropdown-placement-topRight .zen-dropdown-arrow {
    right: 15%;
}
.zen-dropdown-placement-bottom .zen-dropdown-arrow,
.zen-dropdown-placement-bottomLeft .zen-dropdown-arrow,
.zen-dropdown-placement-bottomRight .zen-dropdown-arrow {
    top: 4px;
    border-bottom-color: white;
}
.zen-dropdown-placement-bottom .zen-dropdown-arrow {
    left: 50%;
}
.zen-dropdown-placement-bottomLeft .zen-dropdown-arrow {
    left: 15%;
}
.zen-dropdown-placement-bottomRight .zen-dropdown-arrow {
    right: 15%;
}
