@import "../../theme";

.zen-form-item {
    padding: 0;
    font-size: 14px;
    margin: 0 0 24px;
}
.zen-input {
    position: relative;
    border: 1px solid $divider;
    border-radius: 4px;
    font-size: 13px;
    line-height: 17px;
    padding: 12px;
    width: 100%;
    font-weight: $weight-extralight;
    color: $grey-mid;
}
.zen-select {
    display: flex;
    position: relative;
    cursor: pointer;
}
.zen-select-selector {
    border: 1px solid $divider;
    border-radius: 4px;
    flex: 1 1 auto;
    font-size: 13px;
    line-height: 17px;
    padding: 12px;
    width: 100%;
    font-weight: $weight-extralight;
    color: $grey-mid;
}
.zen-form-vertical .zen-form-item {
    flex-direction: column;
}
.zen-form-vertical .zen-form-item-label {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 12px;
    font-weight: $weight-semibold;
}
.zen-select-single .zen-select-selector {
    height: 42px;
}
.zen-select-single .zen-select-selection-item {
    position: absolute;
    user-select: none;
    display: flex;
}
.zen-select-single .zen-select-selector .zen-select-selection-search {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 12px;
}
.zen-select-arrow {
    position: absolute;
    right: 12px;
    top: 18px;
}
.zen-select-dropdown {
    position: absolute;
    z-index: 1050;
}
.zen-select-dropdown-hidden {
    display: none;
}
.zen-select-single .zen-select-selector .zen-select-selection-search-input {
    width: 100%;
}
.zen-select-show-search.zen-select .zen-select-selector {
    cursor: text;
}
.zen-select-multiple .zen-select-selector {
    position: relative;
}
.zen-select-selection-overflow {
    display: flex;
    flex-wrap: wrap;
}
.zen-select-multiple .zen-select-selection-item {
    position: relative;
    max-width: 100%;
    background: $divider;
    padding-left: 3px;
    padding-right: 3px;
    margin-right: 8px;
    border-radius: 4px;
    color: $grey-mid;
    font-size: 13px;
    line-height: 17px;
    user-select: none;
}
.zen-select-selection-item-remove {
    display: none;
}
.zen-select-multiple .zen-select-selection-search {
    position: relative;
    margin-left: 1px;
}
.zen-select-multiple .zen-select-selector .zen-select-selection-search-input {
    min-width: 4px;
    width: 100%;
    border: none;
}
.zen-select-multiple .zen-select-selection-search-mirror {
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    white-space: pre;
}
.rc-virtual-list {
    border: 1px solid $divider;
    border-radius: 4px;
    box-shadow: $box-shadow;
}
.zen-select-item-option {
    border-bottom: 1px solid $divider;
    background: $white;
    font-size: 13px;
    line-height: 17px;
    color: $grey-mid;
    padding: 12px;
    display: flex;
}
.zen-select-item-option-selected {
    background: $primary-10;
}
.zen-select-item-option-content {
    flex: 1 1 auto;
}