@import "../../theme";

.zen-card {
    box-shadow: $box-shadow;
    border-radius: $rounded-10;
    background: $white;
}

.zen-card-head {
    border-bottom: 1px solid $divider;
    font-size: 16px;
    line-height: 24px;
    padding: 24px 32px;
    color: $primary-navy-light;
}

.zen-card-head-wrapper {
    display: flex;
}

.zen-card-head-title {
    flex: 1 1 auto;
    font-weight: $weight-semibold;
}

.zen-card-head-extra {
    float: right;
}

.zen-card-tabs {
    flex: 1 1 auto;
    font-weight: $weight-semibold;
}

.zen-card-tabs-nav-list {
    display: flex;
    position: relative;
    transition-duration: 300ms;
}

.zen-card-tabs-nav-operations-hidden {
    display: none;
}
.zen-card-tabs-tab {
    font-size: 16px;
    margin-right: 36px;
    cursor: pointer;
    line-height: 1;
}

.zen-card-tabs-dropdown-menu-item-disabled {
    opacity: 0.3;
    cursor: not-allowed;
}


.zen-card-tabs-ink-bar {
    height: 3px;
    position: absolute;
    background: $primary-30;
    pointer-events: none;
    bottom: -10px;
    border-radius: 4px;
    padding-left: 2px;
    padding-right: 2px;
}
.zen-card-tabs-ink-bar-animated {
    transition-property: all;
    transition-duration: 300ms;
}
